body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-outfit: Outfit;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-base: 16px;

  /* Colors */
  --color-white: #fff;
  --color-gold: #ffd500;
  --color-black: #000;
  --color-darkgoldenrod: #b7932b;

  /* Paddings */
  --padding-xl: 20px;
  --padding-10xl: 29px;
  --padding-11xs: 2px;
  --padding-base: 16px;

  /* Border radiuses */
  --br-smi: 13px;
}
